@import url(https://fonts.googleapis.com/css2?family=Bungee&family=Bungee+Inline&family=Work+Sans&display=swap);
:root {
  --big-margin: 24px;
  --small-margin:  16px;
  --dark-blue: #1257BF;
  --mid-blue: #5392CC;
  --dark-red: #D85858;
  --mid-red: #E08989;
  --bg: #FAF2E6;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FAF2E6;
  background-color: var(--bg);
  font-size: min(calc(3px + 0.85vw), 16px);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important; 
  -webkit-clip-path: inset(50%) !important;
    clip-path: inset(50%) !important;  
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}



.step > div {
	margin-bottom: calc(var(--big-margin) * 4);
}

.step p {
	color:  black;
	margin:  0;
}

.step h3 {
	color: transparent;
	text-shadow: 0 0 1px var(--dark-blue);
	margin: 0px 0px var(--small-margin) 0px;

}

.step:nth-child(even) h3{
	text-shadow: 0 0 1px var(--mid-blue);
}
.contentWrapper {
  margin:  0 auto;
  max-width: 1350px;
}

.content {
  margin: var(--big-margin);
}

.content > * > *:not(.sr-only):not(.heroVideo) {
  margin-top:  var(--small-margin);
  margin-bottom: 0px;
}

.fullWidth {
  width:  100%;
}

main {
  width:  calc(80% - calc(var(--big-margin) * 2));
  margin: calc(var(--big-margin) * 7) auto;  
}

h1, h2, h3 {
  font-family: 'Bungee';
}

h2 {
  font-size: 4.25em;
  line-height: 95%;
  color: transparent;
  text-shadow: 0 0 1px var(--mid-blue);
}

h3 {
  font-size: 4em;
  line-height: 125%;
}

p {
  font-size: 2em;
  font-family: 'Work Sans';
}

.heroVideo {
  aspect-ratio: 16 / 9;
  width: 100%;
  background: linear-gradient(180deg, #9CC5EB 0%, rgba(156, 197, 235, 0) 100%);
  position: relative;
  margin:  2em 0;
}

.navList {
  font-family: 'Bungee';
  font-size: 3em;
  line-height: 95%;
  counter-reset: css-counter 0;
}

.navList span {
  color: transparent;
  text-shadow: 0 0 1px var(--dark-blue);
  counter-increment: css-counter 1;
  margin-right: 16px;
}

.navList span:before {
  content: counter(css-counter);
  font-size: 0.45em;
  display: inline-block;
  vertical-align: bottom;
  width: 1.9em;
  text-align: center;
  background-image: url(/holo-assistant/static/media/dark-heart.940d9d48.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 54%;
  margin: 0 16px 0 0;
  color: var(--bg);
}

.navList span:nth-child(even){
  text-shadow: 0 0 1px var(--mid-blue);
}

.navList span:nth-child(even):before {
  background-image: url(/holo-assistant/static/media/mid-heart.16a2ae42.svg);
}

.overlay {
  width: 100%;
  height: 100%;
  position: relative;
}

.frameOverlay {
  position: absolute;
  width: 105%;
  height: 105%;
  left: -2.5%;
  top: -2.5%;

}

.drawing {
  position: absolute;
  bottom: 0.4%;
  right: 0px;
  width: 75%;
}

@media all and (max-width: 1200px) {
  .navList span:before {
    background-image: none;
    font-size: 1em;
    width:  0.5em;
    color: var(--dark-blue);
  }

  .navList span:nth-child(even):before {
    background-image: none;
    color: var(--mid-blue);
  }

}
